import React, { useState } from "react";
import { useForm } from "react-hook-form";
import Sidebar from "@/components/Sidebar";
import { Divider } from "@nextui-org/divider";
import { Input, Button, Checkbox } from "@nextui-org/react";
import { PencilIcon, PlusIcon } from "@heroicons/react/24/outline";
import * as Yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";

export default function Page() {
  const [showNewAddressForm, setShowNewAddressForm] = useState(false);
  const [isRepresentingCompany, setIsRepresentingCompany] = useState(false);
  const [showNewCompanyForm, setShowNewCompanyForm] = useState(false);

  // Inicializáljuk az addresses és companies tömböket
  const [addresses, setAddresses] = useState([]);
  const [companies, setCompanies] = useState([]);

  // Yup validációs séma
  const addressSchema = Yup.object().shape({
    newAddressCountry: Yup.string().required("Country is required"),
    newAddressCity: Yup.string().required("City is required"),
    newAddressCounty: Yup.string().required("County is required"),
    newAddressPostalCode: Yup.string().required("Postal Code is required"),
    newAddressStreet: Yup.string().required("Street is required"),
  });

  const companySchema = Yup.object().shape({
    newCompanyName: Yup.string().required("Company Name is required"),
    newCompanyCode: Yup.string().required("Company Code is required"),
  });

  // Formuláris validációs beállítás
  const { register, handleSubmit, reset, formState: { errors } } = useForm({
    resolver: yupResolver(addressSchema),
  });

  const onSubmit = (data) => {
    console.log(data);
    // Cím hozzáadása a címekhez
    setAddresses((prev) => [...prev, data]);
    reset();
    setShowNewAddressForm(false);
  };

  return (
    <div className="flex">
      <Sidebar />
      <div className="flex-1 p-8">
        <h2 className="text-2xl font-bold mb-2">Personal info</h2>
        <p>Update your personal details here.</p>
        <Divider className="my-6 bg-[#EAECF0]" />

        {/* Name Section */}
        <form onSubmit={handleSubmit(onSubmit)}>
          {/* Addresses */}
          <div className="flex items-start w-full mb-6 gap-[32px]">
            <span className="text-[14px] font-semibold min-w-[200px]">Addresses</span>
            <div>
              {addresses.map((address, index) => (
                <div key={index} className="border border-[#D0D5DD] p-4 rounded-md relative w-[343px] mb-4">
                  <h3 className="text-sm font-semibold text-[#344054]">Address {index + 1}</h3>
                  <p>{address.newAddressCountry}</p>
                  <p>{address.newAddressCity}</p>
                  <p>{address.newAddressCounty}</p>
                  <p>{address.newAddressPostalCode}</p>
                  <p>{address.newAddressStreet}</p>
                  <Button
                    variant="light"
                    startContent={<PencilIcon className="h-4 w-4 text-[#344054]" />}
                    className="absolute top-2 right-2 flex items-center gap-1 text-[#344054]"
                    onClick={() => alert('Edit address clicked!')}
                  >
                    Edit
                  </Button>
                </div>
              ))}

              {!showNewAddressForm && (
                <Button
                  variant="light"
                  className="font-semibold mt-4 text-[#1D48E5]"
                  endContent={<PlusIcon className="h-4 w-4 text-[#1D48E5]" />}
                  onClick={() => setShowNewAddressForm(true)}
                >
                  Add new address
                </Button>
              )}

              {showNewAddressForm && (
                <div className="mt-4">
                  <h3 className="text-sm font-semibold text-[#344054] mb-4">Add New Address</h3>
                  <div className="mb-4">
                    <Input {...register("newAddressCountry")} placeholder="Country" fullWidth variant="bordered" />
                    {errors.newAddressCountry && <p className="text-red-500">{errors.newAddressCountry.message}</p>}
                  </div>
                  <div className="mb-4">
                    <Input {...register("newAddressCity")} placeholder="City" fullWidth variant="bordered" />
                    {errors.newAddressCity && <p className="text-red-500">{errors.newAddressCity.message}</p>}
                  </div>
                  <div className="mb-4">
                    <Input {...register("newAddressCounty")} placeholder="County" fullWidth variant="bordered" />
                    {errors.newAddressCounty && <p className="text-red-500">{errors.newAddressCounty.message}</p>}
                  </div>
                  <div className="mb-4">
                    <Input {...register("newAddressPostalCode")} placeholder="Postal Code" fullWidth variant="bordered" />
                    {errors.newAddressPostalCode && <p className="text-red-500">{errors.newAddressPostalCode.message}</p>}
                  </div>
                  <div className="mb-4">
                    <Input {...register("newAddressStreet")} placeholder="Street" fullWidth variant="bordered" />
                    {errors.newAddressStreet && <p className="text-red-500">{errors.newAddressStreet.message}</p>}
                  </div>
                </div>
              )}
            </div>
          </div>

          <Divider className="my-6 bg-[#EAECF0]" />

          {/* Companies */}
          <div className="flex items-start w-full mb-6 gap-[32px]">
            <span className="text-[14px] font-semibold min-w-[200px]">Companies</span>
            <div>
              <Checkbox
                isSelected={isRepresentingCompany}
                onChange={() => setIsRepresentingCompany(!isRepresentingCompany)}
              >
                Are you representing a company?
              </Checkbox>

              {isRepresentingCompany && (
                <div>
                  {companies.map((company, index) => (
                    <div key={index} className="border border-[#D0D5DD] p-4 rounded-md relative w-[343px] mt-4 mb-4">
                      <h3 className="text-sm font-semibold text-[#344054]">{company.name}</h3>
                      <p>Code: {company.code}</p>
                      <Button
                        variant="light"
                        startContent={<PencilIcon className="h-4 w-4 text-[#344054]" />}
                        className="absolute top-2 right-2 flex items-center gap-1 text-[#344054]"
                        onClick={() => alert('Edit company clicked!')}
                      >
                        Edit
                      </Button>
                    </div>
                  ))}

                  {!showNewCompanyForm && (
                    <Button
                      variant="light"
                      className="font-semibold mt-4 text-[#1D48E5]"
                      endContent={<PlusIcon className="h-4 w-4 text-[#1D48E5]" />}
                      onClick={() => setShowNewCompanyForm(true)}
                    >
                      Add new company
                    </Button>
                  )}

                  {showNewCompanyForm && (
                    <div className="mt-4">
                      <h3 className="text-sm font-semibold text-[#344054] mb-4">Add New Company</h3>
                      <div className="mb-4">
                        <Input {...register("newCompanyName")} placeholder="Company Name" fullWidth variant="bordered" />
                        {errors.newCompanyName && <p className="text-red-500">{errors.newCompanyName.message}</p>}
                      </div>
                      <div className="mb-4">
                        <Input {...register("newCompanyCode")} placeholder="Company Code" fullWidth variant="bordered" />
                        {errors.newCompanyCode && <p className="text-red-500">{errors.newCompanyCode.message}</p>}
                      </div>
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>

          <Divider className="my-6 bg-[#EAECF0]" />

          <div className="flex justify-end gap-[12px]">
            <Button
              type="button"
              className="bg-white border border-[#D0D5DD] text-[#344054] font-semibold"
              onClick={() => alert('Cancel clicked!')}
            >
              Cancel
            </Button>
            <Button
              type="submit"
              className="bg-blue-600 text-white font-semibold"
              onClick={() => alert('Save clicked!')}
            >
              Save
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}
